import {Component, OnDestroy, Inject, OnInit} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {navAdminItems, navItems, navStaffItems} from '../../_nav';
import {SocialUser} from 'angularx-social-login';
import {Router} from '@angular/router';


@Component({
  selector: 'app-dashboard',
  templateUrl: './default-layout.component.html'
})
export class DefaultLayoutComponent implements OnDestroy, OnInit {
  navigationItems: any[];
  public navItems = navItems;
  public navStaffItems = navStaffItems;
  public navAdminItems = navAdminItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;
  public userData: SocialUser;
  userRole: any[];
  public tempPict = localStorage.getItem('photoUrl') !== null ? localStorage.getItem('photoUrl') : 'assets/img/avatars/pro-icon.png';
  public tempName = localStorage.getItem('email');
  photoUrl: any;
  constructor(@Inject(DOCUMENT) _document?: any, private router?: Router) {
    this.userRole = JSON.parse(localStorage.getItem('userRoles'));
    const existStaff = this.userRole.some(function(item) {
      return item.name.includes('ROLE_STAFF');
    });
    const existAdmin = this.userRole.some(function(item) {
      return item.name.includes('ROLE_SUPER_ADMIN');
    });
    if (existStaff) {
      this.navigationItems = navStaffItems;
    } else if (existAdmin) {
      this.navigationItems = navAdminItems;
    }
    // this.userData = JSON.parse(data);
 //   this.userData.name = localStorage.getItem('name');
   // this.userData.email = localStorage.getItem('email');
    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }

  ngOnInit(): void {

  }

  logOut() {
    localStorage.removeItem('userData');
    localStorage.clear();
    this.router.navigate(['/login']);
  }

  ngOnDestroy(): void {
    this.changes.disconnect();
  }
}
